import axios from 'axios';
import $ from "jquery";

export default {
  // state 
  // actions, mutations, getters 
  namespaced: true, //  actions, mutations, getters 
  state: {
    // 
    products: [],  
    product: {},
    categories: [],
  },
  actions: {
    //  method 
    getProducts(context, page = 1) {
      const api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/products/all`;
      context.commit('LOADING', true, { root: true });
      axios.get(api).then((response) => {
        context.commit('PRODUCTS', response.data.products);
        context.commit('CATEGORIES', response.data.products);
        console.log(':', response);
        context.commit('LOADING', false, { root: true });
      });
    },
    getProduct(context, id) {
      const api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/product/${id}`;
      axios.get(api).then((response) => {
        context.commit('PRODUCT', response.data.product);
        $("#productModal").modal("show");
      });
    },
  },
  mutations: {
    // state computed
    PRODUCTS(state, payload) {
      state.products = payload;
    },
    PRODUCT(state, payload) {
      state.product = payload;
    },
    PAGINATION(state, payload) {
      state.pagination = payload;
    },
    CATEGORIES(state, payload) {
      const categories = new Set();
      payload.forEach((item) => {
        categories.add(item.category);
      });
      state.categories = Array.from(categories);
    },
  },
  getters: {
    products(state) {
      return state.products;
    },
    product(state) {
      return state.product;
    },
    categories(state) {
      return state.categories;
    },
  },
};



