<template>
  <div>
    <Banner />
    <!--  -->
    <h1 class="text-center my-5"></h1>
    <div class="my-5 row justify-content-center">
      <validation-observer class="col-md-6" v-slot="{ invalid, handleSubmit }">
        <form @submit.prevent="handleSubmit(createOrder)">
          <validation-provider
            rules="required|email"
            v-slot="{ errors, classes }"
          >
            <div class="form-group">
              <!--  -->
              <label for="email">Email</label>
              <input
                id="email"
                type="email"
                name="Email"
                placeholder="Email"
                class="form-control"
                v-model="form.user.email"
                :class="classes"
              />
              <!--  -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </div>
          </validation-provider>

          <validation-provider rules="required" v-slot="{ errors, classes }">
            <div class="form-group">
              <!--  -->
              <label for="username"></label>
              <input
                id="username"
                type="text"
                name=""
                placeholder=""
                class="form-control"
                v-model="form.user.name"
                :class="classes"
              />
              <!--  -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </div>
          </validation-provider>

          <validation-provider
            rules="required|numeric|min:10"
            v-slot="{ errors, classes }"
          >
            <div class="form-group">
              <label for="usertel"></label>
              <input
                type="tel"
                class="form-control"
                name=""
                id="usertel"
                v-model="form.user.tel"
                placeholder=""
                :class="classes"
              />
              <!--  -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </div>
          </validation-provider>

          <validation-provider rules="required" v-slot="{ errors, classes }">
            <div class="form-group">
              <!--  -->
              <label for="useraddress"></label>
              <input
                id="useraddress"
                type="address"
                name=""
                placeholder=""
                class="form-control"
                v-model="form.user.address"
                :class="classes"
              />
              <!--  -->
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </div>
          </validation-provider>

          <div class="form-group">
            <label for="useraddress"></label>
            <textarea
              name=""
              id=""
              class="form-control"
              cols="30"
              rows="10"
              v-model="form.message"
            ></textarea>
          </div>
          <div class="d-flex justify-content-between">
            <button type="submit" class="btn btn-primary" @click="backToCart">
              
            </button>
            <button type="submit" class="btn btn-danger" :disabled="invalid">
              
            </button>
          </div>
        </form>
      </validation-observer>
    </div>
    <Footer />
  </div>
</template>

<script>
import Banner from "../Banner";
import Footer from "../Footer";

export default {
  components: {
    Banner,
    Footer,
  },
  data() {
    return {
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: "",
        },
        message: "",
      },
    };
  },
  methods: {
    createOrder() {
      const vm = this;
      const url = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/order`;
      const order = vm.form;
      vm.$store.state.isLoading = true;
      this.$http.post(url, { data: order }).then((response) => {
        // console.log("", response);
        if (response.data.success) {
          vm.$router.push(`/customer_checkout/${response.data.orderId}`);
          vm.$store.dispatch('getCart');
        }
        vm.$store.state.isLoading = false;
      });
    },
    backToCart() {
      const vm = this;
      vm.$router.push("/cart");
    },
  },
};
</script>


