<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openModal(true)">
        
      </button>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="150"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">
            {{ item.origin_price | currency }}
          </td>
          <td class="text-right">
            {{ item.price | currency }}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else></span>
          </td>
          <td>
            <button
              class="btn btn-outline-primary btm-sm"
              @click="openModal(false, item)"
            >
              
            </button>
            <button
              class="btn btn-outline-danger btm-sm"
              @click="openDelModal(item)"
            >
              
            </button>
          </td>
        </tr>
      </tbody>
    </table>

    <!--  -->
    <!-- pagination   -->
    <Pagination @passFunction="getProducts"></Pagination>

    <!-- Modal -->
    <!--  -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>{{ changeTitle }}</span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    placeholder=""
                    v-model="tempProduct.imageUrl"
                  />
                </div>
                <div class="form-group">
                  <label for="customFile"
                    > 
                    <i
                      class="fas fa-spinner fa-spin"
                      v-if="status.fileUploading"
                    ></i>
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                    style="padding-bottom: 35px"
                  />
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid"
                  alt=""
                  :src="tempProduct.imageUrl"
                />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder=""
                    v-model="tempProduct.title"
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      placeholder=""
                      v-model="tempProduct.category"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      placeholder=""
                      v-model="tempProduct.unit"
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                      v-model="tempProduct.origin_price"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      placeholder=""
                      v-model="tempProduct.price"
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    placeholder=""
                    v-model="tempProduct.description"
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    placeholder=""
                    v-model="tempProduct.content"
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    />
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click="updateProduct"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div
      class="modal fade"
      id="delProductModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ delTitle }}</strong>
            ()
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button type="button" class="btn btn-danger" @click="delProduct">
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination";

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      products: [],
      tempProduct: {},
      isLoading: false,
      isNew: false,
      changeTitle: "",
      nowPage: "",
      delId: "",
      delTitle: "",
      status: {
        fileUploading: false,
      },
    };
  },
  methods: {
    getProducts(page = 1) {
      const api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then((response) => {
        // console.log(response.data);
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.nowPage = response.data.pagination.current_page;
        //pagination
        vm.$bus.$emit("page:change", response.data.pagination);
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
        this.changeTitle = "";
      } else {
        this.tempProduct = Object.assign({}, item);
        this.isNew = false;
        this.changeTitle = "";
      }
      $("#productModal").modal("show");
    },
    openDelModal(item) {
      $("#delProductModal").modal("show");
      const vm = this;
      vm.delId = item.id;
      vm.delTitle = item.title;
    },
    updateProduct() {
      let api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/admin/product`;
      let httpMethods = "post";
      const vm = this;
      if (!vm.isNew) {
        api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/admin/product/${vm.tempProduct.id}`;
        httpMethods = "put";
      }
      this.$http[httpMethods](api, { data: vm.tempProduct }).then(
        (response) => {
          // console.log(response.data);
          if (response.data.success) {
            vm.$store.dispatch("updateMessage", {
              message: response.data.message,
              status: "success",
            });
            $("#productModal").modal("hide");
            vm.getProducts(vm.nowPage);
          } else {
            vm.$store.dispatch("updateMessage", {
              message: response.data.message,
              status: "danger",
            });
            $("#productModal").modal("hide");
            vm.getProducts();
            // console.log("");
          }
          // vm.products = response.data.products;
        }
      );
    },
    delProduct() {
      const vm = this;
      const api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/admin/product/${vm.delId}`;
      this.$http.delete(api).then((response) => {
        if (response.data.success) {
          vm.$store.dispatch("updateMessage", {
            message: response.data.message,
            status: "success",
          });
          $("#delProductModal").modal("hide");
          vm.getProducts();
        } else {
          vm.$store.dispatch("updateMessage", {
            message: response.data.message,
            status: "dander",
          });
          $("#delProductModal").modal("hide");
          vm.getProducts();
          // console.log("");
        }
      });
    },
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0];
      const vm = this;
      const formData = new FormData();
      formData.append("file-to-upload", uploadedFile);
      const url = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/admin/upload`;
      vm.status.fileUploading = true;
      this.$http
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((response) => {
          if (response.data.success) {
            vm.status.fileUploading = false;
            vm.$store.dispatch("updateMessage", {
              message: response.data.message,
              status: "success",
            });
            // vm.tempProduct  imageUrl
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
          } else {
            vm.$store.dispatch("updateMessage", {
              message: response.data.message,
              status: "danger",
            });
          }
        });
    },
  },
  created() {
    this.getProducts();
  },
};
</script>


