import axios from 'axios';
import router from '../router';
import '../bus';
import $ from "jquery";

export default {
  // state 
  // actions, mutations, getters 
  // namespaced: true, //  actions, mutations, getters 
  state: {
    // 
    cart: {
      carts: [],
    },
  },
  actions: {
    //  method 
    getCart(context) {
      context.commit('LOADING', true, { root: true });
      const api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/cart`;
      axios.get(api).then((response) => {
        if (response.data.data.carts) {
          context.commit('CART', response.data.data);
        }
        context.commit('LOADING', false, { root: true });
        console.log('', response.data.data);
      });
    },
    addtoCart(context, { id, qty }) {
      const api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/cart`;
      context.commit('LOADING', true, { root: true });
      const item = {
        product_id: id,
        qty,
      };
      axios.post(api, { data: item }).then((response) => {
        context.commit('LOADING', false, { root: true });
        context.dispatch('getCart');
        context.dispatch('updateMessage', {
          message: response.data.message,
          status: 'success',
        },);
        console.log(':', response);
        $("#productModal").modal("hide");
      });
    },
    removeCart(context, id) {
      const api = `${process.env.API_PATH}/api/${process.env.VUETEST_PATH}/cart/${id}`;
      context.commit('LOADING', true, { root: true });
      axios.delete(api).then((response) => {
        context.commit('LOADING', false, { root: true });
        context.dispatch('getCart');
        console.log('', response);
        context.dispatch('updateMessage', {
          message: response.data.message,
          status: 'danger',
        },);
        // bus.$emit("message:push", response.data.message, "danger");
      });
    },   
    goConnection() {
      router.push("/connection");
    },
  },
  mutations: {
    // state computed
    CART(state, payload) {
      state.cart = payload;
    }, 
  },
  getters: {
    cart(state) {
      return state.cart;
    }, 
  },
};



